import Vue from 'vue'
import Router from 'vue-router'

import Home from 'components/Home'
import Blog from 'components/Blog'
import MyWork from 'components/MyWork'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/home',
    component: Home,
    alias: '/'
  },{
    path: '/blog',
    component: Blog
  },{
    path: '/mywork',
    component: MyWork
  }]
})



